<template>
<div class="card">
  <div class="card-header">
    Exam08View
  </div>
  <div class="card-body">
    <div>kind : {{route.params.kind}}</div>
    <div>color: {{route.params.color}}</div>
    <hr>
    <div>kind : {{route.query.kind}}</div>
    <div>color: {{route.query.color}}</div>
  </div>
</div>
</template>

<script setup>
  import { useRoute } from 'vue-router';
  import { toRaw } from 'vue';
  
  const route = useRoute();
  console.log(route);
  console.log("------------------");
  // vue3에서는 대부분의 객체가 proxy객체로 리턴된다. 
  console.log(toRaw(route)); // proxy객체를 간단하게!
  console.log("------------------");
  console.log(JSON.parse(JSON.stringify(route))); // proxy객체를 더더 간단하게!
  console.log("kind:  ", route.params.kind);
  console.log("color: ", route.params.color);
</script>

